.mapsContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.mapTitle {
    position: absolute;
    left: 0;
    top: 4px;
}

.map {
    position: relative;
    border-bottom: 1px solid lightgray;
}

.maps {
    position: relative;
}

.mapLoading {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.815);
    z-index: 1;
}

.mapLoading > div {
    display: inline-block;
    font-size: 2em;
    margin: auto;
}


